import provinceData from './city-data/province.js';
import getCity from './city-data/city.js';
import getArea from './city-data/area.js';
import getStreets from './city-data/streets.js';
export default {
	data() {
		return {
			province: "请选择",
			city: "请选择",
			area: "请选择",
			street: "请选择",
			cityDataAll: '', // 市的所有數據
			getAreaAll: '', // 区的所有数据
			getStreetsAll: '', // 街道的所有数据
			presentIndex: [],
			proIndex: 0,
			cityIndex: 0,
			areaIndex: 0,

			provinceData: '', // 当前展示的省数据
			cityData: '', // 当前展示的市数据
			areaData: '', //当前展示的区数据
			streetsData: '', //当前展示的区数据
			showIndex: 0,
			heightCot: 0,
			bb: ''
		}
	},
	created() {
		uni.getSystemInfo({
			success: res => {
				console.log(res.safeArea.height)
				this.heightCot = (res.safeArea.height * 2) / 2
			}
		})
	},
	mounted() {
		// console.log(provinceData)
		this.provinceData = provinceData;
		this.cityDataAll = getCity;
		this.getAreaAll = getArea;
		this.getStreetsAll = getStreets
	},
	methods: {
		anewSelect(num) {
			switch (num) {
				case 0:
					this.showIndex = 0;
					this.areaData = [];
					this.streetsData = [];
					this.city = '请选择';
					this.area = '请选择';
					this.street = '请选择'
					break;
				case 1:
					this.showIndex = 1;
					this.areaData = [];
					this.streetsData = [];
					this.area = '请选择';
					this.street = '请选择'
					break;
				case 2:
					this.showIndex = 2;
					this.streetsData = [];
					this.street = '请选择'
					break;
				case 3:
					console.log(11111);
					break;
			}
		},
		selectPro(index, label) {
			console.log(index)
			const {
				cityDataAll
			} = this;
			this.proIndex = index; // 当前省的下标
			this.province = label;
			this.cityData = cityDataAll[index];
			this.showIndex = 1;

		},
		selectCity(index, label) {
			const {
				proIndex
			} = this;
			// console.log(this.getAreaAll)
			this.city = label;
			this.cityIndex = index; // 当前市的下标
			this.areaData = this.getAreaAll[proIndex][index];
			this.showIndex = 2;
		},
		selectaArea(index, label) {
			const {
				proIndex,
				cityIndex
			} = this;
			this.area = label;
			this.showIndex = 3;
			console.log(index)
			console.log(this.getStreetsAll[proIndex][cityIndex])
			console.log(this.getStreetsAll[proIndex][cityIndex][index])
			this.streetsData = this.getStreetsAll[proIndex][cityIndex][index]
		},
		selectStreet(index, label) {
			this.street = label;
			const {
				province,
				city,
				area,
				street
			} = this;
			this.$emit('conceal',{
				province,
				city,
				area,
				street
			})
			// this.showIndex = 4;
		}
	}
}
